.wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.right {
  position: absolute;
  top: 12px;
  right: 12px;
  flex: 1;
  height: calc(100vh - 70px);
  background: #ffffff;
  overflow: hidden;
}

.tagItem {
  border-bottom: 1px solid #e8e8e8;
  padding: 8px 12px 8px 24px;
}

.tagInfo {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tagName {
  font-size: 16px;
  font-weight: 500;
  color: #333333;
}

.tagType {
  font-size: 12px;
  color: #999999;
}

.tagLabels {
  max-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
